.projects {
    -webkit-column-count: 3;
    -moz-column-count: 3;
    column-count: 3;
    -webkit-column-width: 33%;
    -moz-column-width: 33%;
    column-width: 33%;
    padding: 0 12px;
}

.projects .pics {
    -webkit-transition: all 350ms ease;
    transition: all 350ms ease;
    cursor: pointer;
    margin-bottom: 12px;
}

.projects .pics:hover {
    filter: opacity(.8)
}

@media (max-width: 991px) {
    .projects {
        -webkit-column-count: 2;
        -moz-column-count: 2;
        column-count: 2;
    }
}

@media (max-width: 480px) {
    .projects {
        -webkit-column-count: 1;
        -moz-column-count: 1;
        column-count: 1;
        -webkit-column-width: 100%;
        -moz-column-width: 100%;
        column-width: 100%;
    }
}

.model{
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: left;
    align-items: center;
    background-color: black;
    transition: opacity .4s ease, visibility .4s ease, tranform .5 ease-in-out;
    visibility: hidden;
    opacity: 0;
    transform: scale(0);
    overflow: hidden;
    z-index: 999;
}

.model.open {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
}

.model img {
    border-radius: 45px;
    width: auto;
    max-width: 80%;
    height: auto;
    max-height: 80%;
    display: flex;
    flex-direction: row;
    align-items: center;
    line-height: 0;
    box-sizing: border-box;
    padding: 20px 30px 20px;
}

.model h1, p {
    display: flex;
    color: white;
    margin-right: 10px;
}

.model a:link, a:visited {
    background-color: #9c9c9c;
    color: white;
    border-radius: 10px;
    padding: 14px 25px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
}

.model.open svg {
    position: fixed;
    top: 10px;
    right: 10px;
    width: 2rem;
    height: 2rem;
    padding: 5px;
    background-color: rgba(0, 0, 0, 0.4);
    color: white;
    cursor: pointer;
}